@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  background-color: #ffffff;
  min-width: 320px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  margin: 0;
  height: 100%;
}

img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  height: auto;
}

iframe {
  border: none;
  display: block;
  width: 100%;
}

html {
  height: 100%;
}

.advantages {
  padding-top: 32px;
  min-width: 320px;
}
@media (min-width: 768px) {
  .advantages {
    padding-top: 47px;
  }
}
@media (min-width: 1200px) {
  .advantages {
    padding-top: 59px;
  }
}
.advantages img {
  width: 100%;
  height: 100%;
}

.advantages__header {
  padding-left: 18px;
  padding-right: 18px;
  box-sizing: border-box;
  padding-bottom: 16px;
}
@media (min-width: 768px) {
  .advantages__header br {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages__header {
    padding-left: 124px;
    padding-right: 124px;
    padding-bottom: 55px;
    font-size: 21px;
  }
}
@media (min-width: 1200px) {
