    padding-left: 357px;
    padding-right: 359px;
    padding-bottom: 44px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 767px) {
  .advantages__list br {
    display: none;
  }
}

.advantages__intro {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  margin: 0;
  margin-top: 35px;
}

.advantages__title {
  font-size: 18px;
  line-height: 26px;
  margin: 0;
}

.advantages__number {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  margin-top: 11px;
  display: block;
}

.advantages__description {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  margin: 0;
  margin-top: 17px;
}
@media (min-width: 768px) {
  .advantages__description {
    margin-top: 24px;
  }
}

.advantages__description--padding {
  padding-left: 10px;
  padding-right: 10px;
}

.advantages__city {
  background-color: #81b3d2;
  padding-top: 71px;
  padding-bottom: 37px;
  padding-left: 18px;
  padding-right: 18px;
  min-height: 211px;
  min-width: 256px;
  box-sizing: border-box;
  position: relative;
}
@media (max-width: 767px) {
  .advantages__city::before {
    content: "";
    position: absolute;
    background-image: url("/img/city-triangle.svg");
    background-repeat: no-repeat;
    width: 100%;
    height: 57px;
    left: 0;
    top: -10px;
    background-position: center;
  }
}
@media (min-width: 768px) {
  .advantages__city {
    grid-column: 1/2;
    padding-top: 52px;
    padding-left: 33px;
    padding-right: 33px;
    padding-bottom: 30px;
  }
}
@media (min-width: 1200px) {
  .advantages__city {
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 51px;
  }
}

.advantages__bridge {
  background-color: #81b3d2;
  padding-top: 37px;
  padding-bottom: 37px;
  padding-left: 18px;
  padding-right: 18px;
  min-height: 191px;
  min-width: 256px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .advantages__bridge {
    grid-column: 3/4;
    grid-row: 1;
    padding-top: 52px;
    padding-left: 33px;
    padding-right: 33px;
    padding-bottom: 30px;
  }
}
@media (min-width: 1200px) {
  .advantages__bridge {
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 51px;
  }
}

.advantages__title--optional {
  color: #ffffff;
}

.advantages__number--optional {
  color: #ffffff;
}

.advantages__description--optional {
  color: #ffffff;
}

.advantages__item--color {
  background-color: #eeeeee;
  padding-left: 26px;
  padding-right: 26px;
  padding-top: 26px;
  padding-bottom: 24px;
  min-height: 165px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .advantages__item--color {
    padding-top: 44px;
    padding-left: 33px;
    padding-right: 33px;
    padding-bottom: 44px;
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .advantages__item--color {
    padding-top: 52px;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 52px;
