}

@media (min-width: 768px) {
  .advantages__item:nth-child(1) {
    grid-column: 1/4;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 768px) {
  .advantages__item:nth-child(2) {
    grid-column: 1/4;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__image--city {
  overflow: hidden;
  grid-column: 2/4;
}
@media (min-width: 768px) {
  .advantages__image--city img {
    height: 256px;
  }
}

.advantages__image--bridge {
  grid-column: 1/3;
}
@media (min-width: 768px) {
  .advantages__image--bridge img {
    height: 256px;
  }
}

@media (max-width: 767px) {
  .advantages__list .advantages__br {
    display: block;
  }
