.social {
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  max-width: 280px;
  flex-wrap: wrap;
  margin-top: 24px;
}
@media (min-width: 768px) {
  .social {
    margin-top: 0;
  }
}

.social__item {
  margin-left: 3px;
  margin-right: 3px;
  margin-top: 6px;
  width: 48px;
  height: 48px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .social__item {
    margin-top: 3px;
    margin-bottom: 3px;
  }
}

.social__item--vkontakte {
  position: relative;
  display: block;
  background-color: #81b3d2;
}
.social__item--vkontakte::before {
  content: "";
  display: block;
  width: 48px;
  height: 48px;
  mask-image: url("/img/social-vk.svg#social-vk");
  position: absolute;
  background-color: #ffffff;
}

.social__item--vkontakte:hover {
  background-color: #669ec0;
}

.social__item--vkontakte:active {
  background-color: #5496bd;
}
.social__item--vkontakte:active::before {
  background-color: rgba(255, 255, 255, 0.3);
}

.social__item--telegram {
  position: relative;
  display: block;
  background-color: #81b3d2;
}
.social__item--telegram::before {
  content: "";
  position: absolute;
  mask-image: url("/img/social-tg.svg#social-tg");
  width: 48px;
  height: 48px;
  display: block;
  background-color: #ffffff;
}

.social__item--telegram:hover {
  background-color: #669ec0;
}

.social__item--telegram:active {
  background-color: #5496bd;
}
.social__item--telegram:active::before {
  background-color: rgba(255, 255, 255, 0.3);
}

.social__item--youtube {
  position: relative;
  display: block;
  background-color: #81b3d2;
}
.social__item--youtube::before {
  content: "";
  display: block;
  width: 48px;
  height: 48px;
  mask-image: url("/img/social-youtube.svg#social-youtube");
  position: absolute;
  background-color: #ffffff;
}

.social__item--youtube:hover {
  background-color: #669ec0;
}

.social__item--youtube:active {
  background-color: #5496bd;
}
.social__item--youtube:active::before {
  background-color: rgba(255, 255, 255, 0.3);
}

