.intro {
  background-image: url("/img/photo-mountains.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #82b3d3;
  background-position: center;
  min-width: 320px;
  min-height: 389px;
  box-sizing: border-box;
  padding-top: 90px;
  position: relative;
}
@media (min-width: 768px) {
  .intro {
    padding-top: 77px;
    padding-bottom: 91px;
    background-image: url("/img/photo-mountains-tablet.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .intro {
    background-image: url("/img/photo-mountains-tablet@2x.jpg");
  }
}
@media (min-width: 768px) {
  .intro img {
    min-width: 461px;
    min-height: 350px;
  }
}
@media (min-width: 1200px) {
  .intro {
    background-image: url("/img/photo-mountains-desktop@1x.jpg");
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .intro {
    background-image: url("/img/photo-mountains-desktop@2x.jpg");
  }
}
.intro::before {
  content: "";
  position: absolute;
  background-image: url("/img/intro-triangle.svg");
  background-repeat: no-repeat;
  width: 100%;
  height: 57px;
  left: 0;
  bottom: 0;
  background-position: center;
}

@media (max-width: 767px) {
  .page-nav {
    position: relative;
  }
}
@media (max-width: 767px) {
  .page-nav::before {
    content: "";
    position: absolute;
    background-image: url("/img/icon-menu.svg");
    width: 22px;
    height: 22px;
    top: 17px;
    right: 20px;
  }
}
.page-nav__wrapper {
  width: 100%;
  min-height: 56px;
  z-index: 2;
}
@media (max-width: 767px) {
  .page-nav__wrapper {
    background-color: #81b3d2;
    position: absolute;
  }
}

.page-nav__link {
  text-decoration: none;
  color: #ffffff;
}
@media (min-width: 768px) {
  .page-nav__link {
    color: #000000;
  }
}
@media (min-width: 768px) {
  .page-nav__link:hover {
    color: #81b3d2;
  }
}
.page-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}

@media (min-width: 768px) {
  .page-nav__link--current {
    color: #766357;
  }
}

.page-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .page-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-flow: dense;
    background-color: #ffffff;
    min-height: 56px;
    align-items: center;
  }
}
.page-nav__list::before {
  content: "";
  grid-column: 3/4;
  grid-row: span 2;
}

@media (max-width: 767px) {
  .page-nav__wrapper--closed {
    display: none;
  }
}

@media (max-width: 767px) {
  .page-nav__wrapper--opened::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (max-width: 767px) {
  .page-nav__wrapper--opened::after {
    content: "";
    position: absolute;
    background-image: url("/img/icon-cross.svg");
    width: 22px;
    height: 22px;
    top: 17px;
    right: 20px;
  }
}

.page-nav__wrapper--nojs .page-nav__wrapper--closed {
  display: none;
}

