.location {
  display: grid;
  position: relative;

  iframe {
    grid-row: 1/2;
    grid-column: 1/2;
    position: relative;
  }

  img {
    grid-row: 1/2;
    grid-column: 1/2;
    width: 100%;
  }

  &::before {
    content: "";
    position: absolute;
    width: 27px;
    height: 27px;
    background-image: url("/img/map-marker.svg");
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.location__map {
  grid-column: 1/2;
  grid-row: 1/2;
}
